/* ========================
   Page-C 样式
   第三页布局及内容样式
======================== */
/* 图片层样式 */
/* 动态 GIF 样式 */
.learn-gif {
  position: absolute; /* 确保绝对定位 */
  top: 35%; /* 调整到页面顶部 40% 的位置 */
  left: 40%;
  transform: translate(-50%, -50%); /* 水平和垂直居中 */
  width: 60%; /* 调整宽度 */
  height: auto; /* 保持比例 */
  z-index: 1; /* 在文字层和背景层之间 */
}

.shelf {
  position: absolute; /* 确保绝对定位 */
  top: 70%; /* 调整到页面顶部 40% 的位置 */
  left: 50%;
  transform: translate(-50%, -50%); /* 水平和垂直居中 */
  width: 110%; /* 调整宽度 */
  height: auto; /* 保持比例 */
  z-index: 1; /* 在文字层和背景层之间 */
}

.page-c {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    padding: 0;
    gap: 5vh;
    background-color: #ece9c7; /* 设置背景颜色 */
    box-sizing: border-box;
  }
  

  /* 右对齐文本 框*/
  .right-box-3 {
    margin-top: 10vh;
    text-align: right; /* 右对齐文本 */
    margin-right: 5vw; /* 与页面右侧的距离 */
    box-sizing: border-box; /* 包括内边距和边框计算宽度 */
  } 

  .right-box-3 h2 {
    color:#355F2E;
    font-weight: bold; /* 加粗文字 */
  }

  .right-box-3 h3 {
    font-size: 1.2em;
    line-height: 1;
    color: #71a843; /* 覆盖全局样式的颜色 */
  }

  .right-box-3 p {
    color: #155e54;
    line-height: 1.4; /* 正文行距 */
    margin: 4px 0; /* 段落间距 */
  }
 
/* ========================
   媒体查询
======================== */
@media (max-width: 400px) {
    .right-box-3 h2 {
        font-size: calc(1.6rem + 0.4vw); /* 标题字体大小，动态适配 */
        line-height: 12px; /* 标题行距 */
        margin-bottom: 20px; /* 标题与正文的间距 */
      }

    .right-box-3 h3 {
        font-size: calc(1.2rem + 0.4vw);
        line-height: 5px;
      }

    .right-box-3 p,
    .left-box-3 p {
      font-size: calc(0.8rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  
  @media (min-width: 401px) and (max-width: 600px) {
    .right-box-3 h2 {
        font-size: calc(2.2rem + 0.4vw); /* 标题字体大小，动态适配 */
        line-height: 18px; /* 标题行距 */
        margin-bottom: 20px; /* 标题与正文的间距 */
      }

      .right-box-3 h3 {
        font-size: calc(1.6rem + 0.4vw); 
        line-height: 8px;
      }

    .right-box-3 p,
    .left-box-3 p {
      font-size: calc(1.2rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  
  @media (min-width: 601px) {
    .right-box-3 h2 {
        font-size: calc(3rem + 0.4vw); /* 标题字体大小，动态适配 */
        line-height: 25px; /* 标题行距 */
        margin-bottom: 30px; /* 标题与正文的间距 */
      }

      .right-box-3 h3 {
        font-size: calc(2.4rem + 0.4vw);
        line-height: 10px;
      }

    .right-box-3 p,
    .left-box-3 p {
      font-size: calc(1.8rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  