/* ========================
   Page-C 样式
   第三页布局及内容样式
======================== */

.page-17 {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    padding: 0;
    background-color: #fbddf7; /* 设置背景颜色 */
    box-sizing: border-box;
  }
  
  /* 居中对齐文本 框*/
  .center-box-17 {
    margin-top: 30vh;
    text-align: center; /* 右对齐文本 */
    box-sizing: border-box; /* 包括内边距和边框计算宽度 */
  } 

  .center-box-17 h2 {
    color:#c33946;
    font-weight: bold; /* 加粗文字 */
  }

  .center-box-17 h3 {
    line-height: 1;
    color: #dd7272; /* 覆盖全局样式的颜色 */
  }

  .center-box-17 p {
    color: #7c1523;
    line-height: 1.4; /* 正文行距 */
    margin: 4px 0; /* 段落间距 */
  }

  .highlight17 {
    color: #dd7272; 
    font-weight: bold; /* 加粗字体 */
  }
 
/* ========================
   媒体查询
======================== */
@media (max-width: 400px) {
    .center-box-17 h2 {
        font-size: calc(1.6rem + 0.4vw); /* 标题字体大小，动态适配 */
        line-height: 12px; /* 标题行距 */
        margin-bottom: 20px; /* 标题与正文的间距 */
      }

    .center-box-17 h3 {
        font-size: calc(1.2rem + 0.4vw);
        line-height: 5px;
      }

    .center-box-17 p,
    .left-box-3 p {
      font-size: calc(0.8rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  
  @media (min-width: 401px) and (max-width: 600px) {
    .center-box-17 h2 {
        font-size: calc(2.2rem + 0.4vw); /* 标题字体大小，动态适配 */
        line-height: 18px; /* 标题行距 */
        margin-bottom: 20px; /* 标题与正文的间距 */
      }

      .center-box-17 h3 {
        font-size: calc(1.6rem + 0.4vw); 
        line-height: 8px;
      }

    .center-box-17 p {
      font-size: calc(1.2rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }

  @media (min-width: 601px) {
    .center-box-17 h2 {
        font-size: calc(3rem + 0.4vw); /* 标题字体大小，动态适配 */
        line-height: 25px; /* 标题行距 */
        margin-bottom: 30px; /* 标题与正文的间距 */
      }

      .center-box-17 h3 {
        font-size: calc(2.4rem + 0.4vw);
        line-height: 10px;
      }

    .center-box-17 p{
      font-size: calc(1.8rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  