/* ========================
   Page-12 样式
======================== */
/* 图片层样式 */
/* 动态 GIF 样式 */
.animation-gif {
  position: absolute; /* 确保绝对定位 */
  top: 55%; /* 调整到页面顶部 40% 的位置 */
  left: 50%;
  transform: translate(-50%, -50%); /* 水平和垂直居中 */
  width: 90%; /* 调整宽度 */
  height: auto; /* 保持比例 */
  z-index: 1; /* 在文字层和背景层之间 */
}

/* 父容器样式：Page-B */
.page-14 {
    height: 100vh; /* 页面高度为视口高度 */
    display: flex; /* 使用 Flex 布局 */
    flex-direction: column; /* 子元素从上到下排列 */
    justify-content: flex-start; /* 子元素从顶部开始排列 */
    align-items: stretch; /* 子元素宽度覆盖父容器 */
    padding: 0; /* 内边距为 0 */
    gap: 25vh; /* 左右文本框之间的间距 */
    box-sizing: border-box; /* 确保边距和内边距不影响宽度 */
    background-color: #f5e7cd; /* 设置背景颜色 */
  }
  
  /* 左对齐文本框 */
  .left-box-14 {
    margin-top: 10vh;
    text-align: left; /* 左对齐文本 */
    margin-left: 5vw; /* 与页面左侧的距离 */
    box-sizing: border-box; /* 包括内边距和边框计算宽度 */
  }
  
  .left-box-14 h2 {
    color:#196d9b;
    font-weight: bold; /* 加粗文字 */
  }

  .left-box-14 h3 {
    color: #47a3c5; /* 覆盖全局样式的颜色 */
  }
  
  .left-box-14 p {
    color: #1c4d68;
    line-height: 1.4; /* 正文行距 */
    margin: 4px 0; /* 段落间距 */
  }

  /* 右对齐文本 框*/
  .right-box-14 {
    margin-top: 10vh;
    text-align: right; /* 右对齐文本 */
    margin-right: 5vw; /* 与页面右侧的距离 */
    box-sizing: border-box; /* 包括内边距和边框计算宽度 */
  } 

  .right-box-14 h2 {
    color:#196d9b;
    font-weight: bold; /* 加粗文字 */
  }

  .right-box-14 h3 {
    color: #47a3c5; /* 覆盖全局样式的颜色 */
  }

  .right-box-14 p {
    color: #1c4d68;
    line-height: 1.4; /* 正文行距 */
    margin: 4px 0; /* 段落间距 */
  }

.highlight14 {
  color:#196d9b;
  font-weight: bold; /* 加粗文字 */
}
  /* ========================
   Page-4 媒体查询
======================== */
@media (max-width: 400px) {
    .left-box-14 h2,
    .right-box-14 h2 {
      font-size: calc(1.6rem + 0.4vw); /* 标题字体大小，动态适配 */
      line-height: 12px; /* 标题行距 */
      margin-bottom: 20px; /* 标题与正文的间距 */
    }
  
     /* 关键词*/
     .left-box-14 h3,
     .right-box-14 h3 {
        font-size: calc(1.2rem + 0.4vw);
        line-height: 10px;
    }

    .right-box-14 p,
    .left-box-14 p {
      font-size: calc(0.8rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  
  @media (min-width: 401px) and (max-width: 600px) {
    .left-box-14 h2,
    .right-box-14 h2 {
      font-size: calc(2.2rem + 0.4vw); /* 标题字体大小，动态适配 */
      line-height: 18px; /* 标题行距 */
      margin-bottom: 20px; /* 标题与正文的间距 */
    }
  
     /* 关键词*/
     .left-box-14 h3,
     .right-box-14 h3 {
       font-size:  calc(1.6rem + 0.4vw); 
       line-height: 12px;
    }

    .right-box-14 p,
    .left-box-14 p {
      font-size: calc(1.2rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  
  @media (min-width: 601px) {
    .left-box-14 h2,
    .right-box-14 h2 {
      font-size: calc(3rem + 0.4vw); /* 标题字体大小，动态适配 */
      line-height: 25px; /* 标题行距 */
      margin-bottom: 30px; /* 标题与正文的间距 */
    }

    /* 关键词*/
    .left-box-14 h3,
    .right-box-14 h3 {
     font-size: calc(2.4rem + 0.4vw);
     line-height: 16px;
   }
  
    .right-box-14 p,
    .left-box-14 p {
      font-size: calc(1.8rem + 0.4vw); /* 正文字体大小，动态适配 */
    }
  }
  