/* ========================
   全局基础样式
======================== */

/* 设置全局的样式，包括字体、背景颜色和禁止页面滚动 */
html, body {
  overflow: hidden; /* 禁止滚动，避免出现滚动条 */
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif; /* 全局字体 */
  box-sizing: border-box; /* 全局设置为 border-box 模型 */
}


/* ========================
   全局动画效果
======================== */

/* 翻页动画效果 */
/* 背景层容器样式 */
.background-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0; /* 背景层放在最底层 */
  background-size: cover;
  background-position: center;
}

/* 背景层淡入淡出动画 */
.background-transition-enter {
  opacity: 0; /* 初始状态透明 */
}

.background-transition-enter-active {
  opacity: 1; /* 最终状态完全可见 */
  transition: opacity 500ms ease-in-out; /* 动画时长 */
}

.background-transition-exit {
  opacity: 1; /* 初始状态完全可见 */
}

.background-transition-exit-active {
  opacity: 0; /* 最终状态透明 */
  transition: opacity 1000ms ease-in-out; /* 动画时长 */
}


/* 网易云式文字动画 */
.textAni {
  animation-name: textAniKey; /* 关联的关键帧动画 */
  animation-duration: 1.5s; /* 动画时长为 1.5 秒 */
  animation-timing-function: cubic-bezier(0, 0, 0.5, 1); /* 贝塞尔曲线控制动画节奏 */
  animation-iteration-count: 1; /* 动画只播放一次 */
  animation-fill-mode: both; /* 保留动画结束状态 */
}

/* 动画定义：网易云文字效果 */
@keyframes textAniKey {
  0% {
    transform: translateY(2vw); /* 初始位置向下偏移 2vw */
    opacity: 0; /* 初始状态透明 */
  }
  100% {
    transform: translateY(0); /* 最终位置回到原点 */
    opacity: 1; /* 最终状态完全可见 */
  }
}

 /* 箭头图标样式 */
 .arrow {
  position: absolute;
  bottom: 5vh; /* 距离页面底部 */
  left: 50%; /* 水平居中 */
  transform: translateX(-50%);
  width: 40px; /* 图标宽度 */
  height: 40px; /* 图标高度 */
  background-image: url('../images/UpArrow.png'); /* 替换为你的图标路径 */
  background-size: contain; /* 图标完全适配 */
  background-repeat: no-repeat; /* 防止重复显示 */
  opacity: 0; /* 初始隐藏 */
  z-index: 10;
  animation: fadeInOut 2s infinite; /* 添加时隐时现动画 */
  animation-delay: 3s; /* 延迟动画开始 */
}

/* 定义时隐时现动画 */
@keyframes fadeInOut {
  0%, 100% {
    opacity: 0; /* 起始和结束透明 */
  }
  50% {
    opacity: 1; /* 中间完全可见 */
  }
}

/* 小红花样式 */
.flower {
  display: inline-block; /* 保证旋转效果生效 */
  font-size: 2rem; /* 默认字体大小 */
  color: #fe9f7c; /* 粉红色 */
  animation: spinFlower 3s linear infinite; /* 旋转动画 */
}

/* 小红花旋转动画定义 */
@keyframes spinFlower {
  0% {
    transform: rotate(0deg); /* 起始不旋转 */
  }
  100% {
    transform: rotate(360deg); /* 完成一整圈旋转 */
  }
}

button {
  display: block;
  position: fixed; /* 固定在页面右上角 */
  top: 20px; /* 距离顶部 20px */
  right: 20px; /* 距离右侧 20px */
  background: transparent; /* 背景透明 */
  color: rgb(69, 69, 69);
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}


